/*Layout Styles*/
.page-banner {
    height: 50vh;
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    background-color: $gray;
    padding: 0 40px;

    @media (min-width: 1200px){
      align-items: center;
    }
    h1 {
       font-size: clamp(16px, 6vw, 48px);
       line-height: 1.4;
       margin: 0 0 10px 0;
    }
    p {
        margin-bottom: 0;
        line-height: 1.4;
    }
    .post-edit-link {
        display: block;
        clear: left;
        padding: 12px 10px 12px 0;

            @media (min-width: 1200px){
            	text-align: center;
            }

    }
}

.bio-photo {
    width: 250px;
    height: 250px;
    border-radius: 50%;
}


